div.rbc-time-view div.rbc-event-label {
  display: none !important;
}
a.rbc-show-more {
  display: none;
}
div.rbc-btn-group {
  display: inline-block;
  margin-bottom: 10px;
}
b.rbc-toolbar-label {
  margin-bottom: 10px;
}
@media only screen and (max-width: 360px) {
  div.rbc-toolbar {
    display: inline-flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
  }
}
@media only screen and (min-width: 361px) {
  div.rbc-toolbar {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
  }
}